/**
 * SEO component that queries for data with
 *  Gatsby's useStaticQuery React hook
 *
 * See: https://www.gatsbyjs.com/docs/use-static-query/
 */
import React, { Fragment, useState, useEffect } from "react";
import PropTypes from "prop-types";
import { useLocation } from "@reach/router";
import { useStaticQuery, graphql } from "gatsby";
import Helmet from "react-helmet";
import { utils } from "@core/helper";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { collectionKeys } from "@core/helper/utils";
import { getDocDataFilter } from "@core/helper/config";

function Seo(props) {
  const {
    title,
    metaTitle: metaTitleProp,
    description,
    image,
    children,
    noIndex,
    ogTitle,
    ogDescription,
    ogType,
    twitterTitle,
    twitterDescription,
  } = props;
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            description
            keywords
            author
            siteUrl
          }
        }
      }
    `
  );

  const [metaData, setMetaData] = useState({
    title: title,
    description: description,
    image: image,
    ogTitle: ogTitle,
    ogDescription: ogDescription,
    ogType: ogType,
    twitterTitle: twitterTitle,
    twitterDescription: twitterDescription,
  });

  const location1 = useLocation();
  console.log("location1::", location1);
  const slugUrl = location1.pathname.split("/")?.[0];
  console.log("slugUrl::", slugUrl);

  // Fetch data from the API
  useEffect(() => {
      // const app = initializeApp(site?.siteMetadata?.firestore);
      // const db = getFirestore(app);

      const fetchData = async () => {
        console.log("called::");
        // const resp = await getDocDataFilter(collectionKeys.META, "slug", slugUrl)
        // console.log("resp::", resp);

        // Simulate an API call with setTimeout
        // setTimeout(() => {
        //   const response = {
        //     title: "Dynamic Title from API",
        //     description: "Dynamic description from API response.",
        //   };

        //   setMetaData({
        //     title: response.title || "Default Title",
        //     description: response.description || "Default Description",
        //   });
        // }, 2000); // Simulated 2-second delay
      };
      fetchData();
  }, []);

  const location = useLocation();
  const defaultTitle = site.siteMetadata?.title;
  const metaTitle = metaTitleProp || defaultTitle;
  const ogTitleAlt = ogTitle || metaTitle;
  const metaDescription = description || site.siteMetadata.description;
  const ogDescriptionAlt = ogDescription || metaDescription;
  const ogTypeAlt = ogType || "website";
  const ogImage = image;
  const twitterTitleAlt = twitterTitle || metaTitle;
  const twitterDescAlt = twitterDescription || metaDescription;
  const currentUrl = location?.href || site.siteMetadata.siteUrl;

  return (
    <Fragment>
      <Helmet>
        <title>{metaData.title}</title>
        <meta name="description" content={metaData.description} />
      </Helmet>
      <title>{title ? title : defaultTitle}</title>
      <meta name="title" content={metaTitle} />
      <meta name="description" content={metaDescription} />
      <meta property="og:locale" content={"en_US"} />
      <meta property="og:title" content={ogTitleAlt} />
      <meta property="og:description" content={ogDescriptionAlt} />
      <meta property="og:image" content={site.siteMetadata.siteUrl + ogImage} />
      <meta property="og:type" content={ogTypeAlt} />
      <meta property="og:url" content={currentUrl} />
      <meta name="twitter:card" content="summary" />
      <meta name="twitter:creator" content={site.siteMetadata?.author || ``} />
      <meta name="twitter:title" content={twitterTitleAlt} />
      <meta name="twitter:description" content={twitterDescAlt} />
      <meta
        name="robots"
        content={`${
          noIndex ? "noindex" : "index"
        }${", follow, max-image-preview:large, max-snippet:-1, max-video-preview:-1"}`}
      />
      {children}
    </Fragment>
  );
}

Seo.defaultProps = {
  description: ``,
};

Seo.propTypes = {
  description: PropTypes.string,
  title: PropTypes.string.isRequired,
  image: PropTypes.string.isRequired,
};

export default Seo;
