const { initializeApp } = require("firebase/app");
const {
  doc,
  getDocs,
  collection,
  getDoc,
  getFirestore,
  query,
  where,
  addDoc,
  updateDoc,
  increment,
} = require("firebase/firestore/lite");
const moment = require("moment");
const { collectionKeys } = require("./utils");

const firebaseConfig = {
  apiKey: process.env.FIREBASE_API_KEY,
  authDomain: process.env.FIREBASE_AUTH_DOMAIN,
  projectId: process.env.FIREBASE_PROJECT_ID,
  storageBucket: process.env.FIREBASE_STORAGE_BUCKET,
  messagingSenderId: process.env.FIREBASE_MESSAGE_SENDER_ID,
  appId: process.env.FIREBASE_APP_ID,
};

const app = initializeApp(firebaseConfig);
const db = getFirestore(app);

const addCount = async (db, key) => {
  try {
    const clients = collection(db, collectionKeys.VIEWCOUNT);
    await addDoc(clients, {
      count: 1,
      key: key,
      lastUpdate: moment().format(),
    });
  } catch (err) {
    console.log("err add::", err);
  }
};

const getDocDataDb = async (path) => {
  try {
    if (db) {
      const querySnapshot = await getDocs(collection(db, path));
      const data = querySnapshot.docs.map((doc) => ({
        id: doc.id, // Each document id
        ...doc.data(), // Spread the document data
      }));
      // console.log("data::", data);
      return data;
    }
  } catch (err) {
    console.log(`err getDoc`, err);
  }
};

const getDocDetails = async (collectionName, id) => {
  const docRef = await doc(db, collectionName, id);
  const docSnap = await getDoc(docRef);
  if (docSnap.exists()) {
    console.log("docSnap::", docSnap.data());
    return docSnap.data();
  }
  return;
};

const getDocData = async (path) => {
  try {
    if (db) {
      const querySnapshot = await getDocs(collection(db, path));
      const data = querySnapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
      }));
      return data;
    }
  } catch (err) {
    console.log(`err getDoc`, err);
  }
};

const getDocsFirebase = async (key) => {
  try {
    const q = query(collection(db, collectionKeys.VIEWCOUNT), where("key", "==", key));
    const querySnapshot = await getDocs(q);
    return querySnapshot;
  } catch (err) {
    console.log(`err getDoc`, err);
  }
};

const getDocDataFilter = async (path, key, value) => {
  try {
    const queryData = await query(
      collection(db, path),
      where(key, "==", value)
    );
    const querySnapshot = await getDocs(queryData);
    const data = querySnapshot.docs.map((doc) => ({
      id: doc.id,
      ...doc.data(),
    }));
    console.log("data::", data);

    return data;
  } catch (err) {
    console.log(`err getDoc`, err);
  }
};

const updateFirebaseCount = async (docId, key) => {
  try {
    const docRef = doc(db, collectionKeys.VIEWCOUNT, docId);
    await updateDoc(docRef, {
      count: increment(1),
      lastUpdate: moment().format(),
    });
    let querySnapshot = await getDocsFirebase(db, key);
    let count;
    querySnapshot?.docs?.forEach(async (doc) => {
      count = doc.data();
    });
    return count;
  } catch (e) {
    console.error("Error updating document: ", e);
  }
};

module.exports = {
  addCount,
  getDocDetails,
  getDocDataDb,
  getDocDataFilter,
  getDocData,
  getDocsFirebase,
  updateFirebaseCount,
};
